<template>
  <div class="products Project_product">
    <div id="loadingDiv" v-show="value">
      <div class="loader"></div>
    </div>

    <div id="myModal" class="modal" v-show="modal">
      <!-- Modal content -->
      <div class="modal-content">
        <div class="justify-content-center">
          <p style="margin-bottom: 0">
            <span v-html="text" class="icon-modal"></span>

            <span style="font-size: 20px">{{ msg }}</span>
          </p>
        </div>
      </div>
    </div>

    <div class="container pb-5" v-if="products[0]">
      <div class="d-sm-flex justify-content-sm-end mb-3">
        <div class="d-lg-flex align-items-lg-center d-none mr-3">
          <select class="form-control" v-model="sort" @change="SelectSort()">
            <option value="" disabled>商品排序</option>
            <option value="low_high">價格由低至高</option>
            <option value="high_low">價格由高至低</option>
          </select>
        </div>
      </div>
      <div class="row">
        <div
          class="col-lg-3 col-md-4 mb-5 col-sm-6" id="products"
          v-for="item in filterProducts"
          :key="item.id"
        >
          <router-link
            :to="`/product/${item.id}`"
            class="text-decoration-none text-dark"
          >
            <div class="card">
              <div class="product__wrap">
                <div
                  class="product__wrap__img bg-cover position-relative"
                  style="
                    height: 200px;
                    background-position: center;
                    background-size: cover;
                  "
                  :style="{ backgroundImage: `url(${item.imageUrl[0]})` }"
                >
                  <span
                    class="badge badge-secondary align-self-start favorite"
                    style="border-radius: 0 !important"
                  >
                    {{ item.category }}
                  </span>
                </div>
              </div>
              <div class="card-body">
                <div class="d-flex justify-content-between align-items-center">
                  <h5 class="card-title text-dark font-weight-bold">
                    {{ item.title }}
                  </h5>
                </div>

                <div class="float-left">
                  <span class="mb-0 h4 font-weight-bold text-danger"> $</span>
                  <span class="mb-0 h4 font-weight-bold text-danger mr-1">{{
                    item.price
                  }}</span>
                </div>
                <div class="float-right">
                  <span @click.prevent="addfavorite(item)">
                    <i class="fa-sharp fa-solid fa-heart"></i>
                  </span>
                  <span @click.prevent="addToCart(item.id)"
                    ><i class="fas fa-cart-plus mr-1"></i
                  ></span>
                </div>
              </div>
            </div>
          </router-link>
        </div>
      </div>
      <pagination
        :pages="pagination"
        @update="getProducts"
        v-if="products[0]"
      ></pagination>
    </div>
  </div>
</template>

<script>
import pagination from "@/components/MyPagination.vue";
export default {
  name: "MyProducts",
  data() {
    return {
      msg: "",
      text: "",
      modal: false,
      products: [],
      value: true,
      favoriteList: [],
      favoriteId: [],
      sort: "",
      pagination: {},
    };
  },
  created() {
    this.$emit("changeCategory", this.$route.params.category);
    this.getProducts();
  },
  props: ["category"],
  components: {
    pagination,
  },
  methods: {
    getProducts(page) {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/products?page=${page}`;
      this.$http.get(api).then((res) => {
        this.products = res.data.data;
        this.pagination = res.data.meta.pagination;
        this.value = false;
      });
    },
    addToCart(id, quantity = 1) {
      const api = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/shopping`;
      const cart = {
        product: id,
        quantity,
      };
      this.$http
        .post(api, cart)
        .then(() => {
          this.$emit("updateCart");
          this.text =
            '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
          this.msg = "商品已加入購物車！";
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
        })
        .catch(() => {
          this.text =
            '<i class="fa-solid fa-triangle-exclamation" style="color: red;"></i>';
          this.msg = "購物車已存在該商品！";
          // this.msg = errors.response.data.errors[0]; //catch裡的括號要加errors
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
        });
    },
    addfavorite(item) {
      this.favoriteList.push(item);
      this.favoriteId.push(item.id);
      this.favoriteList = [...new Set(this.favoriteList)];
      this.favoriteId = [...new Set(this.favoriteId)];
      localStorage.setItem("favoriteList", JSON.stringify(this.favoriteList));
      localStorage.setItem("favoriteId", JSON.stringify(this.favoriteId));
      this.text =
        '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
      this.msg = "商品已加入收藏清單！";
      this.modal = true;
      this.modal = setTimeout(() => {
        this.modal = false;
      }, 1000 * 1 * 1);
    },
    SelectSort() {
      this.filterProducts.sort((a, b) => {
        if (this.sort === "low_high") {
          return a.price - b.price;
        } else if (this.sort === "high_low") {
          return b.price - a.price;
        }
        return this.filterProducts;
      });
    },
  },
  computed: {
    filterProducts() {
      if (this.category === "所有商品") {
        return this.products;
      }
      return this.products.filter((item) => item.category === this.category);
    },
  },
};
</script>

<style>
.favorite {
  position: absolute;
  left: 0;
}

.fa-heart {
  padding: 5px;
  color: #c8c8c8;
  border-radius: 50%;
  font-size: 20px;
  margin-right: 10px;
}

.fa-cart-plus {
  font-size: 20px;
  background-color: #28a745;
  color: #fff;
  padding: 5px;
  border-radius: 7px;
}

.list-group {
  margin-top: 15px;
  top: 72px;
}

#products .card {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  transition: all 0.5s;
}

#products .card:hover {
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
}

.product__wrap {
  overflow: hidden;
}

/*LOADING */
.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}

.loader {
  margin: auto;
  font-size: 4px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid #4b5f9b;
  border-right: 1.1em solid #4b5f9b;
  border-bottom: 1.1em solid #4b5f9b;
  border-left: 1.1em solid #f8f9fc;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
  transform: translateY(-50%);
  top: 50%;
}

@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

#loadingDiv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f8f9fc;
  z-index: 3;
  overflow: hidden;
}

.main {
  position: relative !important;
  margin-bottom: 0 !important;
}

.modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  z-index: 9999999999;
}

/* Modal Content/Box */
.modal-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  max-width: 100%;
}

.icon-modal {
  font-size: 25px;
  vertical-align: middle;
  margin-right: 5px;
}
</style>
